import React from "react";

export default function useChartConfig({
    googleData = [],
}: {
    googleData?: any;
}) {
    const [state, setState] = React.useState({
        googleData,
        data: makeDataFrom(googleData),
    });

    React.useEffect(() => {
        setState((old) => ({
            ...old,
            data: makeDataFrom(googleData),
        }));
    }, [googleData]);

    return {
        ...state,
    };
}

function makeDataFrom(
    googleData: any,
) {
    return [
        ...new Array(1),
    ].map((d, i) => makeSeries(i, googleData));
}

function makeSeries(
    i: number,
    googleData: any,
) {
    const filterData = googleData.filter((_:string, i: number) => i !== 0);
    return {
        label: `Series ${i + 1}`,
        data: filterData.map((el: string[]) => {
            const secondary = Math.round(+el[1].replace(',', '.'))
            return {
                primary: el[0],
                secondary: secondary,
            };
        }),
    };
}
